document.addEventListener("DOMContentLoaded", function () {
  const studentForm = document.getElementById("studentForm");
  const studentsTable = document
    .getElementById("studentsTable")
    .getElementsByTagName("tbody")[0];
  const searchInput = document.getElementById("search");

  studentForm.addEventListener("submit", function (event) {
    event.preventDefault();
    const newStudent = {
      firstName: document.getElementById("firstName").value,
      lastName: document.getElementById("lastName").value,
      age: document.getElementById("age").value,
      course: document.getElementById("course").value,
      faculty: document.getElementById("faculty").value,
      subjects: document
        .getElementById("subjects")
        .value.split(",")
        .map((s) => s.trim()),
    };
    fetch("/students", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(newStudent),
    })
      .then((response) => response.json())
      .then((data) => {
        alert(data.message);
        loadStudents();
        studentForm.reset();
      });
  });

  searchInput.addEventListener("input", function () {
    const filter = searchInput.value.toLowerCase();
    const rows = studentsTable.getElementsByTagName("tr");
    for (const row of rows) {
      const cells = row.getElementsByTagName("td");
      const lastName = cells[1].textContent.toLowerCase();
      const course = cells[3].textContent.toLowerCase();
      if (lastName.includes(filter) || course.includes(filter)) {
        row.style.display = "";
      } else {
        row.style.display = "none";
      }
    }
  });

  function loadStudents() {
    fetch("/students")
      .then((response) => response.json())
      .then((students) => {
        studentsTable.innerHTML = "";
        students.forEach((student) => {
          const row = studentsTable.insertRow();
          row.innerHTML = `
                        <td>${student.firstName}</td>
                        <td>${student.lastName}</td>
                        <td>${student.age}</td>
                        <td>${student.course}</td>
                        <td>${student.faculty}</td>
                        <td>${student.subjects.join(", ")}</td>
                        <td>
                            <button onclick="editStudent('${
                              student.id
                            }')">Edit</button>
                            <button onclick="deleteStudent('${
                              student.id
                            }')">Delete</button>
                        </td>
                    `;
        });
      });
  }

  window.editStudent = function (id) {
    // Implement edit student functionality here
  };

  window.deleteStudent = function (id) {
    fetch(`/students/${id}`, { method: "DELETE" })
      .then((response) => response.json())
      .then((data) => {
        alert(data.message);
        loadStudents();
      });
  };

  loadStudents();
});
